import React, { useState, useEffect, useContext } from 'react';
import { Button, CardBody, CardHeader, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import logo from '../../../assets/img/brand/logo.png';
import firebase from '../../../firebase/Firebase';
import { UserContext } from '../../../context/contexts';
import Spinner from '../../../components/spinner/Spinner';
import { Redirect } from 'react-router-dom';



function Delete(props) {

    const [password1, setPassword1] = useState('');
    const [startDelete, setStartDelete] = useState(false);
    const [deleted, setDeleted] = useState(false);
    const { email } = useContext(UserContext);

    useEffect(() => {
        if (!email) {
            props.history.replace('/login');
        }
    })

    const handleSubmit = event => {
        event.preventDefault();
        setStartDelete(true);
        var user = firebase.auth().currentUser;
        var credentials = firebase.auth.EmailAuthProvider.credential(
            user.email,
            password1
        );
        user.reauthenticateWithCredential(credentials)
            .then(() => {
                user.delete()
                    .then(() => {
                        setDeleted(true);
                    })

            })
            .catch(() => {
                alert("Cannot delete your account. Please verify if your password is correct.");
            })

    }


    return (
        <div>
            {deleted ? <Redirect to="/register" /> : null}
            <Container>
                <Row className="justify-content-center">
                    <Col className="mt-4" md="9" lg="7" xl="6">
                        <CardHeader>
                            <img src={logo} />
                        </CardHeader>
                        <CardBody className="p-4 bg-white">
                            <strong>Please type your password to complete deletion.</strong>
                            <Form onSubmit={handleSubmit}>
                                <InputGroup id="email" className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="icon-lock"></i>
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input disabled value={email} />

                                </InputGroup>
                                <InputGroup id="password" className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="icon-lock"></i>
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input onChange={e => setPassword1(e.target.value)} type="password" placeholder="Password" />

                                </InputGroup>
                                {startDelete ? <div className="text-center"><Spinner /></div> :
                                    <Button color="warning" block>Delete Account</Button>}
                            </Form>
                        </CardBody>
                    </Col>
                </Row>

            </Container>
        </div>

    )
}


export default Delete;
